section.Upper {
  width: 100%;
  padding: 2rem 0;
  height: 100vh; }
  @media only screen and (max-width: 768px) {
    section.Upper {
      padding: 1rem 0; } }
  section.Upper .dabba {
    position: absolute;
    width: 100%;
    margin: 0;
    padding: 0 5rem;
    bottom: 8rem; }
    @media only screen and (max-width: 768px) {
      section.Upper .dabba {
        padding: 0;
        bottom: 0; } }
    section.Upper .dabba .scroll {
      float: right; }
      section.Upper .dabba .scroll .inner .text {
        margin: 0 auto;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        font-weight: 600;
        text-align: right; }
    section.Upper .dabba .dates .light {
      text-align: right; }
    section.Upper .dabba .dates .heavy {
      line-height: 1;
      text-align: right;
      font-weight: 900;
      font-size: 3rem; }

.nav {
  width: 100%;
  margin: 0;
  padding: 0 10rem; }
  .nav .inner {
    width: 100%; }
