      .nav .inner {
        text-align: center; } }
    .nav .inner .left .text .inner {
      font-size: 1.25rem;
      font-weight: 900;
      font-style: italic;
      text-align: center;
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content; }
    @media only screen and (max-width: 768px) {
      .nav .inner .right {
        display: none; } }
    .nav .inner .right nav .text {
      text-align: right; }

section.Common {
  width: 100%; }
  section.Common .inner {
    width: 100%;
